.home-page {
    width: 100%;
    height: 68rem;
    position: absolute;
    top: 0;
    left: 0;
    display: grid;
    grid-template-columns: 72px repeat(36, 1fr) 72px;
    grid-template-rows: 90px 15% 47.872% 10% 10% 66px;
    justify-self: center;
    align-items: center;
}

#arrow-left-home {
    width: 12px;
    height: 24px;
    grid-column: 2/3;
    grid-row: 3/4;
    justify-self: start;
    align-self: center;
    display: flex;
    flex-flow: column;
    &:hover {
        transform: scale(1.1);
    }
}

#central-window {
    width: 520px;
    height: auto;
    grid-column: 5/19;
    justify-self: end;
    grid-row: 3/4;
    box-shadow:
    0 2.7px 2.2px rgba(0, 0, 0, 0.017),
    0 6.4px 5.3px rgba(0, 0, 0, 0.024),
    0 12px 10px rgba(0, 0, 0, 0.03),
    0 21.4px 17.9px rgba(0, 0, 0, 0.036),
    0 40.1px 33.4px rgba(0, 0, 0, 0.043),
    0 96px 80px rgba(0, 0, 0, 0.06)
  ;
}

#central-catchphrase {
    width: 520px;
    height: 360px;
    grid-column: 20/36;
    grid-row: 3/4;
  & h1 {
    font-size: 36px;
    font-weight: 600;
    letter-spacing: -1.2px;
  }
  & h2 {
    font-size: 23px;
    font-weight: 300;
    margin-top: 24px;
}
}

.central-language-selection {
    width: auto;
    display: flex;
    flex-flow: row nowrap;
    justify-content: left;
    align-items: start;
    margin-top: 72px;
    & span {
    font-size: 18px;
    font-weight: 400;
    margin-right: 12px;
    }
}

.btn-language { 
    width: 87px;
    height: 22px;    
    transform: translateY(2px);
    display: flex;
    flex-flow: row nowrap;
    border-bottom: 2px black solid;
    justify-content: space-between;
    align-items: flex-end;
    & span {
        font-size: 16px;
        font-weight: 400;
        margin-left: 6px;
    }
}


.central-buttons {
    width: auto;
    display: flex;
    flex-flow: row nowrap;
    justify-content: left;
    align-items: start;
    margin-top: 36px;
}


.button-practice {
    width: 120px;
    height: 40px;
    border: 2px black solid;
    border-radius: 8px;
    font-size: 16px;
    font-weight: 400;
    margin-right: 18px;
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
    align-items: center;
    color: inherit;
    text-decoration: inherit;
    &:hover{
        background-color: black;
        color: white;
        border: 2px rgb(255, 255, 255) solid;
    }
}

.button-test {
    width: 120px;
    height: 40px;
    background-color: #FFEF29;
    border: 2px #FFEF29 solid;
    color: black;
    border-radius: 8px;
    font-size: 16px;
    font-weight: 400;

    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
    align-items: center;
    &:hover{
        background-color: black;
        color: white;
        border: 2px rgb(255, 255, 255) solid;
    }
}

#arrow-right-home {
    width: 12px;
    height: 24px;
    grid-column: 37/38;
    grid-row: 3/4;
    justify-self: end;
    align-self: center;
    &:hover {
        transform: scale(1.1);
    }
}

.dots-selectors {
    width: 66px;
    grid-column: 19/20;
    grid-row: 5/6;
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    align-items: center;   
}

#dot1, #dot3 {
    width: 8px;
    height: 8px;
    border: 1px black solid;
    border-radius: 50%;
}

#dot2 {
    width: 8px;
    height: 8px;
    // border: 1px black solid;
    border-radius: 50%;
    background-color: black;
}
